<template>
<div class="page page__03  overview">
  <div class="content content-full flex justify-center relative">

    <div class="badge badge__large mt-12">
      <strong>Modul 1 –</strong>
      GLOBUS E-Learning <br>
      Markthalle.
      <p class="badge__info">Wählen Sie ein Kapitel um fortzufahren.</p>
    </div>
    <div class="mobile--overview">
      <div class="slider">
        <swiper ref="slider" :options="swiperOptions">
          <swiper-slide>
            <router-link to="/chapter01" class="booth booth__01" :class="{'disabled' : chapterStatus[0]}">
              <div class="booth__wrapper relative" :class="{'active' : chapterStatus[0]}">
                <img src="@/assets/booth/stand-5-grün.svg">
                <img src="@/assets/booth/Button-DieVision.svg" class="chapter__btn">
              </div>
            </router-link>
          </swiper-slide>
          <swiper-slide>
            <router-link to="/chapter02" class="booth booth__02" :class="{'disabled' : !chapterStatus[1]}">
              <div class="booth__wrapper relative" :class="{'active' : chapterStatus[1]}">
                <img src="@/assets/booth/stand-9_gelb.svg">
                <img src="@/assets/booth/Button-DieMission.svg" class="chapter__btn">
              </div>
            </router-link>
          </swiper-slide>
          <swiper-slide>
            <router-link to="/chapter04" class="booth booth__03" :class="{'disabled' : !chapterStatus[2]}">
              <div class="booth__wrapper relative" :class="{'active' : chapterStatus[2]}">
                <img src="@/assets/booth/stand-7-groß.svg">
                <img src="@/assets/booth/Button-DieMarkenpyramide.svg" class="chapter__btn">
              </div>
            </router-link>
          </swiper-slide>
          <swiper-slide>
            <router-link to="/chapter05" class="booth booth__04" :class="{'disabled' : !chapterStatus[3]}">
              <div class="booth__wrapper relative" :class="{'active' : chapterStatus[3]}">
                <img src="@/assets/booth/stand-8-rotweiß.svg">
                <img src="@/assets/booth/Button-Leistungen.svg" class="chapter__btn">
              </div>
            </router-link>
          </swiper-slide>
          <swiper-slide>
            <router-link to="/chapter06" class="booth booth__05" :class="{'disabled' : !chapterStatus[4]}">
              <div class="booth__wrapper relative" :class="{'active' : chapterStatus[4]}">
                <img src="@/assets/booth/stand-3-grün.svg">
                <img src="@/assets/booth/Button-Quiz.svg" class="chapter__btn">
              </div>
            </router-link>
          </swiper-slide>
          <div class="swiper-button-prev" slot="button-prev"></div>
          <div class="swiper-button-next" slot="button-next"></div>

        </swiper>
        <div class="swiper-pagination" slot="pagination"></div>
      </div>

    </div>

    <div class="desktop--overview">
      <router-link to="/chapter01" class="booth booth__01" :class="{'disabled' : chapterStatus[0]}">
        <div class="booth__wrapper relative" :class="{'active' : chapterStatus[0]}">
          <img src="@/assets/booth/stand-5-grün.svg">
          <img src="@/assets/booth/Button-DieVision.svg" class="chapter__btn">
        </div>
      </router-link>
      <router-link to="/chapter02" class="booth booth__02" :class="{'disabled' : !chapterStatus[1]}">
        <div class="booth__wrapper relative" :class="{'active' : chapterStatus[1]}">
          <img src="@/assets/booth/stand-9_gelb.svg">
          <img src="@/assets/booth/Button-DieMission.svg" class="chapter__btn">
        </div>
      </router-link>
      <router-link to="/chapter04" class="booth booth__03" :class="{'disabled' : !chapterStatus[2]}">
        <div class="booth__wrapper relative" :class="{'active' : chapterStatus[2]}">
          <img src="@/assets/booth/stand-7-groß.svg">
          <img src="@/assets/booth/Button-DieMarkenpyramide.svg" class="chapter__btn">
        </div>
      </router-link>
      <router-link to="/chapter05" class="booth booth__04" :class="{'disabled' : !chapterStatus[3]}">
        <div class="booth__wrapper relative" :class="{'active' : chapterStatus[3]}">
          <img src="@/assets/booth/stand-8-rotweiß.svg">
          <img src="@/assets/booth/Button-Leistungen.svg" class="chapter__btn">
        </div>
      </router-link>
      <router-link to="/chapter06" class="booth booth__05" :class="{'disabled' : !chapterStatus[4]}">
        <div class="booth__wrapper relative" :class="{'active' : chapterStatus[4]}">
          <img src="@/assets/booth/stand-3-grün.svg">
          <img src="@/assets/booth/Button-Quiz.svg" class="chapter__btn">
        </div>
      </router-link>
      <img src="@/assets/booth/stand-4-kleinerWagen.svg" class="booth booth__06">
      <img src="@/assets/booth/stand-6-blau.svg" class="booth booth__07">
      <img src="@/assets/booth/stand-2-hellgrün.svg" class="booth booth__08">

    </div>


  </div>
  <div class="footer__border"></div>
</div>
</template>

<script>

export default {
  name: "Overview",
  data() {
    return {
      swiperOptions: {
        slidesPerView: 1,
        centeredSlides: true,
        pagination: {
          clickable: true,
          el: '.swiper-pagination'
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.slider.$swiper
    },
    chapterStatus(){
      let status = window.localStorage.getItem('status');
      let s = parseFloat(status);
      let open = 0;
      if(s == 0.2){
        open = 1;
      } else if(s == 0.4){
        open = 2;
      } else if(s == 0.6){
        open = 3;
      } else if(s == 0.8){
        open = 4;
      } else if(s == 1){
        open = 5;
      }
      let out = [];
      for (let i = 0; i < 5; i++) {
        if(open >= i){
          out.push(true)
        } else {
          out.push(false)
        }

      }

      return out
    },
    name(){
      let name = window.localStorage.getItem('name');
      return name
    }
  },

}
</script>